import { GraphNode } from "./graph";
export class Clip {
    constructor() {
        this.length = 1;
        this.extent = 1;
        this.extentPast = 0;
        this.start = 0;
    }
    get totalExtent() {
        return this.extent - this.extentPast;
    }
}
export class ClipInstance extends Clip {
}
export class Track extends GraphNode {
    constructor() {
        super(...arguments);
        this.name = "Track name";
        this.enabled = true;
        this.height = 64;
        this.clips = [];
    }
}
